<template>
  <v-main>
    <v-btn text @click="logout()">登出</v-btn>
    <div class="pt-16"></div>
    <v-card class="mx-auto" width="95%">
      <v-card flat>
        <v-container>
          <v-row>
            <v-col cols="12" md="6">
              <v-card outlined>
                <div class="pt-8"></div>
                <v-row>
                  <v-col
                    class="ml-md-4"
                    v-for="item in card"
                    :key="item.name"
                    cols="3"
                    md="2"
                    @click="choseCard(item)"
                  >
                    <v-img class="mx-auto" :src="item.src" width="50%" />
                  </v-col>
                  <v-col class="ml-4" cols="12">
                    <v-btn color="primary" x-large @click="clearChose()"
                      >清除選擇</v-btn
                    >
                  </v-col>
                </v-row>
                <div class="pt-8"></div>
              </v-card>
            </v-col>
            <v-divider></v-divider>
            <v-col cols="12" md="6">
              <v-row>
                <v-col
                  class="text-center"
                  v-for="(item, index) in selectCard"
                  :key="item.name"
                  cols="6"
                  md="3"
                >
                  <p class="text-h6">第{{ index + 1 }}張</p>
                  <v-img class="mx-auto" :src="item.src" width="50%" />
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
      <div class="pt-12"></div>
      <div class="pt-12"></div>
      <v-row>
        <v-col class="ml-md-8" cols="12" md="6">
          <v-textarea
            v-model="message"
            outlined
            name="input-7-4"
            label="請輸入訊息"
            value=""
          ></v-textarea>
        </v-col>
        <v-col cols="12" md="6">
          <v-btn class="ml-8" color="primary" x-large @click="sendData()"
            >送出</v-btn
          >
        </v-col>
      </v-row>
      <div class="pt-12"></div>
      <div class="pt-12"></div>
    </v-card>
    <div class="pt-16"></div>

    <v-card class="mx-auto" width="95%">
      <v-text-field
        v-model="search"
        outlined
        append-icon="fas fa-search"
        label="搜尋"
        @click:append="searchCardList()"
      ></v-text-field>
      <v-card flat>
        <v-simple-table class="text-center">
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-center">牌型</th>
                <th class="text-center">備註</th>
                <th class="text-center">操作</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in cardList" :key="item.name">
                <td>
                  <div class="d-flex">
                    <v-img
                      v-for="n in 4"
                      :key="n"
                      :src="showListImg(index, n - 1)"
                      width="20"
                    ></v-img>
                  </div>
                </td>
                <td>{{ item.B_Field }}</td>
                <td>
                  <v-btn
                    class="mx-4"
                    color="success"
                    @click="openModifyModal(index)"
                    >修改</v-btn
                  >
                  <v-btn color="error" @click="openRemoveModal(index)"
                    >刪除</v-btn
                  >
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <div class="pt-16"></div>
        <div class="text-center">
          <v-pagination
            v-model="cardPageLen"
            class="my-4"
            prev-icon="mdi-menu-left"
            next-icon="mdi-menu-right"
            :length="totalCardPage"
            :total-visible="8"
            @input="showCardList()"
          ></v-pagination>
        </div>
      </v-card>
    </v-card>
    <div class="pt-16"></div>
    <div class="text-center">
      <v-dialog v-model="dialog">
        <v-card class="mx-auto">
          <v-btn text x-large @click="dialog = false">X</v-btn>
          <v-card flat>
            <v-container>
              <v-row>
                <v-col cols="12" md="6">
                  <v-card outlined>
                    <div class="pt-8"></div>
                    <v-row>
                      <v-col
                        class="ml-md-4"
                        v-for="item in card"
                        :key="item.name"
                        cols="3"
                        md="2"
                        @click="modifyCard(item)"
                      >
                        <v-img class="mx-auto" :src="item.src" width="30" />
                      </v-col>
                      <v-col class="ml-4" cols="12">
                        <v-btn
                          color="primary"
                          x-large
                          @click="clearModifyChose()"
                          >清除選擇</v-btn
                        >
                      </v-col>
                    </v-row>
                    <div class="pt-8"></div>
                  </v-card>
                </v-col>
                <v-divider></v-divider>
                <v-col cols="12" md="6">
                  <v-row>
                    <v-col
                      class="text-center"
                      v-for="(item, index) in selectCardModify"
                      :key="item.name"
                      cols="6"
                      md="3"
                    >
                      <p class="text-h6">第{{ index + 1 }}張</p>
                      <v-img class="mx-auto" :src="item.src" width="30" />
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-container>
          </v-card>
          <div class="pt-8"></div>
          <v-container>
            <v-row>
              <v-col cols="12" md="6">
                <v-textarea
                  v-model="message"
                  outlined
                  name="input-7-4"
                  label="請輸入訊息"
                  value=""
                ></v-textarea>
              </v-col>
              <v-col class="ml-12" cols="12" md="6">
                <v-btn color="primary" x-large @click="fixBtn()">送出</v-btn>
              </v-col>
            </v-row>
          </v-container>
          <div class="pt-12"></div>
          <div class="pt-12"></div>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog v-model="dialog2" width="500">
        <v-card class="mx-auto text-center">
          <div class="pt-8"></div>
          <p>確定要刪除此項目?</p>
          <div class="pt-8"></div>
          <v-btn class="mx-4" color="success" @click="RemoveBtn()">確定</v-btn>
          <v-btn color="error" @click="dialog2 = false">取消</v-btn>
          <div class="pt-8"></div>
        </v-card>
      </v-dialog>
    </div>
  </v-main>
</template>

<script>
export default {
  name: "Home",

  data: () => ({
    card: [
      {
        name: 1,
        src: require("@/assets/skyCard.png"),
      },
      {
        name: 2,
        src: require("@/assets/groundCard.png"),
      },
      {
        name: 3,
        src: require("@/assets/personCard.png"),
      },
      {
        name: 4,
        src: require("@/assets/GooseCard.png"),
      },
      {
        name: 5,
        src: require("@/assets/plumCard.png"),
      },
      {
        name: 6,
        src: require("@/assets/changlanCard.png"),
      },
      {
        name: 7,
        src: require("@/assets/benchCard.png"),
      },
      {
        name: 8,
        src: require("@/assets/JinpingCard.png"),
      },
      {
        name: 9,
        src: require("@/assets/axCard.png"),
      },
      {
        name: 10,
        src: require("@/assets/tallFeetCard.png"),
      },
      {
        name: 11,
        src: require("@/assets/hammerCard.png"),
      },
      {
        name: 12,
        src: require("@/assets/mixed901.png"),
      },
      {
        name: 13,
        src: require("@/assets/mixed902.png"),
      },
      {
        name: 14,
        src: require("@/assets/mixed801.png"),
      },
      {
        name: 15,
        src: require("@/assets/mixed802.png"),
      },
      {
        name: 16,
        src: require("@/assets/mixed701.png"),
      },
      {
        name: 17,
        src: require("@/assets/mixed702.png"),
      },
      {
        name: 18,
        src: require("@/assets/mixed501.png"),
      },
      {
        name: 19,
        src: require("@/assets/mixed502.png"),
      },
      {
        name: 20,
        src: require("@/assets/masterCard01.png"),
      },
      {
        name: 21,
        src: require("@/assets/masterCard02.png"),
      },
      {
        name: 22,
        src: require("@/assets/noneCard.png"),
      },
    ],
    selectCard: [],
    selectCardModify: [],
    selectCardNumber: [],
    selectCardModifyNumber: [],
    message: "",
    cardList: "",
    cardPageLen: 1,
    totalCardPage: 1,
    search: "",
    Aid: "",
    dialog: false,
    dialog2: false,
  }),
  created() {
    this.pageCardList();
  },
  methods: {
    choseCard(chose) {
      if (this.selectCard.length < 4 || this.selectCardNumber.length < 4) {
        this.selectCard.push(chose);
        this.selectCardNumber.push(chose.name);
      } else {
        alert("已經選擇四張牌嘍");
      }
    },
    modifyCard(chose) {
      if (
        this.selectCardModify.length < 4 ||
        this.selectCardModifyNumber.length < 4
      ) {
        this.selectCardModify.push(chose);
        this.selectCardModifyNumber.push(chose.name);
      } else {
        alert("已經選擇四張牌嘍");
      }
      console.log(this.selectCardModifyNumber);
    },
    clearChose() {
      this.selectCard = [];
      this.selectCardNumber = [];
    },
    clearModifyChose() {
      this.selectCardModify = [];
      this.selectCardModifyNumber = [];
    },
    sendData() {
      const api = `https://www.gr8088.com/bot/test`;
      const vm = this;
      var params = new URLSearchParams();
      params.append("A_Field", vm.selectCardNumber);
      params.append("B_Field", vm.message);

      vm.$http
        .post(api, params)
        .then((result) => {
          if (result.data.success) {
            alert("選擇和訊息已送出");
            location.reload();
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
    //分頁處理
    pageCardList() {
      const api = `https://www.gr8088.com/bot/test_Tianjiu_display`;
      const vm = this;
      var params = new URLSearchParams();
      params.append("B_Field", this.search);
      params.append("page", vm.cardPageLen);
      params.append("number", 10);

      vm.$http
        .post(api, params)
        .then((result) => {
          //分頁邏輯處理
          let maxLength = 10;
          vm.totalCardPage = Math.ceil(
            parseInt(result.data.number) / maxLength
          );
          for (let index = 0; index <= vm.totalCardPage; index++) {
            if (index == 1) {
              vm.cardPageLen = index;
              vm.showCardList();
              break;
            }
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //分頁處理過後秀出顯卡列表
    showCardList() {
      const api = `https://www.gr8088.com/bot/test_Tianjiu_display`;
      const vm = this;
      var params = new URLSearchParams();
      params.append("B_Field", this.search);
      params.append("page", vm.cardPageLen);
      params.append("number", 10);

      vm.$http
        .post(api, params)
        .then((result) => {
          vm.cardList = result.data.list;
        })
        .catch((err) => {
          console.error(err);
        });
    },

    showListImg(index, n) {
      let str = this.cardList[index].A_Field;
      let strs = new Array();
      strs = str.split(",");
      for (let index = 0; index < this.card.length; index++) {
        if (strs[n] - 1 == index) {
          return this.card[index].src;
        }
      }
    },

    searchCardList() {
      const api = `https://www.gr8088.com/bot/test_Tianjiu_display`;
      const vm = this;
      var params = new URLSearchParams();
      params.append("B_Field", this.search);
      params.append("page", this.cardPageLen);
      params.append("number", 10);

      this.$http
        .post(api, params)
        .then((result) => {
          let maxLength = 10;
          vm.totalCardPage = Math.ceil(
            parseInt(result.data.number) / maxLength
          );
          if (result.data.success == "true") {
            for (let index = 0; index <= vm.totalCardPage; index++) {
              if (index == 1) {
                vm.cardPageLen = index;
                vm.showCardList();
                break;
              }
            }
          } else {
            alert("查無此搜尋");
            location.reload();
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    openModifyModal(index) {
      this.dialog = true;
      this.Aid = this.cardList[index].id;
    },

    openRemoveModal(index) {
      this.dialog2 = true;
      this.Aid = this.cardList[index].id;
    },

    fixBtn() {
      const api = `https://www.gr8088.com/bot/test_Tianjiu_modify`;
      const vm = this;
      var params = new URLSearchParams();
      params.append("Aid", vm.Aid);
      params.append("A_Field", vm.selectCardModifyNumber);
      params.append("B_Field", vm.message);

      vm.$http
        .post(api, params)
        .then((result) => {
          console.log(result);
          if (result.data.success == "true") {
            alert("修改成功");
            location.reload();
          } else {
            console.log(result.data.error_msg);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    RemoveBtn() {
      const api = `https://www.gr8088.com/bot/test_Tianjiu_DEL`;
      const vm = this;
      var params = new URLSearchParams();
      params.append("Aid", vm.Aid);
      vm.$http
        .post(api, params)
        .then((result) => {
          if (result.data.success) {
            alert("刪除成功");
            location.reload();
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    logout() {
      localStorage.removeItem("bd_token");
      localStorage.removeItem("auth_token");
      location.reload();
    },
  },
};
</script>
